<template>
  <div class="hostservice-disguise-port_information">
    <g-el-table
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      ref="hostservice_disguise_port_information_table"
    >
      <!-- 伪装端口号 -->
      <template v-slot:port="props">
        <el-popover trigger="hover" placement="top">
          <p>{{ dropDownData.type[props.data.row.type] }}</p>
          <div slot="reference" class="name-wrapper">
            {{ props.data.row.port }}
            <!-- <el-tag size="medium">{{ props.data.row.port }}</el-tag> -->
          </div>
        </el-popover>
      </template>
      <template v-slot:port_head="props">
        <el-input
          v-model="params.port"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 协议 -->
      <template slot="protocols" slot-scope="props">
        <div v-if="props.data.row.protocols == 1">TCP</div>
        <div v-else-if="props.data.row.protocols == 2">UDP</div>
      </template>
      <template slot="protocols_head" slot-scope="props">
        <el-select
          v-model="params.protocols"
          clearable
          @change="search"
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.protocols"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 状态 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.success"></span>开启成功
        </div>
        <div v-else-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.error"></span>开启失败
        </div>
      </template>
      <template slot="status_head" slot-scope="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.status"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
    </g-el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      url: `host/${this.layer_data.id}/systemMask/list`,
      type: "post",
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "伪装端口号",
          sortable: true,
          prop: "port",
          second_head: {
            isTemplate: true,
            templateName: "port",
            prop: "port",
            minWidth: "120px",
            templateHeadName: "port_head"
          }
        },
        {
          label: "协议",
          sortable: true,
          prop: "protocols",
          second_head: {
            isTemplate: true,
            templateName: "protocols",
            prop: "protocols",
            minWidth: "150px",
            templateHeadName: "protocols_head"
          }
        },
        {
          label: "状态",
          sortable: true,
          prop: "status",
          second_head: {
            prop: "status",
            isTemplate: true,
            minWidth: "200px",
            templateName: "status",
            templateHeadName: "status_head"
          }
        }
      ],
      params: {
        page: 1,
        limit: 10,
        port: "",
        protocols: "",
        status: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        protocols: [
          {
            id: 1,
            name: "TCP"
          },
          {
            id: 2,
            name: "UDP"
          }
        ],
        status: [
          {
            id: 0,
            name: "开启失败"
          },
          {
            id: 1,
            name: "开启成功"
          }
        ],
        type: {
          udp: "自定义",
          tcp: "自定义",
          middleware: "中间件",
          standard: "标准选项",
          sql: "数据库选项"
        }
      },
      icon: {
        stateIcon: this.$icons.stateIcon
      }
    };
  },
  props: {
    layer_data: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  methods: {
    reload() {
      this.$refs.hostservice_disguise_port_information_table.reload();
    },
    search() {
      this.params.page = 1;
      this.reload();
    }
  }
};
</script>
<style >
.hostservice-disguise-port_information {
  width: 100%;
  padding: 10px;
}
</style>


