import { ajaxPro, reqUnlodingPro } from "req/http/http";

export function editConfig(param) {
  return ajaxPro({
    url: `host/${param.id}/systemMask/config/modify`,
    type: "post",
    data: param
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

