<template>
  <div class="hostservice-disguise-batchLinux">
    <mu-container>
      <mu-form
        :model="formData"
        class="mu-demo-form"
        :label-position="labelPosition"
        label-width="100"
        ref="hostservice_disguise_batchLinux"
      >
        <el-divider content-position="left">
          <span class="batchLinux-divider" v-if="!isLinux"
            >将Windows主机伪装为标准的Linux主机</span
          >
          <span class="batchLinux-divider" v-if="isLinux"
            >将Linux主机伪装为标准的Windows主机</span
          >
        </el-divider>
        <mu-form-item prop="standard" label="标准选项" v-if="isLinux">
          <mu-checkbox
            v-model="formData.standard"
            v-for="item of linuxStandard"
            :key="item.id"
            :value="item.id"
            :label="item.name"
            :disabled="item.id == 3389 ? true : false"
          ></mu-checkbox>
        </mu-form-item>
        <mu-form-item prop="standard" label="标准选项" v-if="!isLinux">
          <mu-checkbox
            v-model="formData.standard"
            v-for="item of windowStandard"
            :key="item.id"
            :value="item.id"
            :label="item.name"
            :disabled="item.id == 22 ? true : false"
          ></mu-checkbox>
        </mu-form-item>
        <mu-form-item prop="sql" label="数据库服务器" v-if="isLinux">
          <mu-radio
            v-model="formData.sql"
            v-for="item of linuxSql"
            :key="item.id"
            :value="item.id"
            :label="item.name"
          ></mu-radio>
        </mu-form-item>
        <mu-form-item prop="sql" label="数据库服务器" v-if="!isLinux">
          <mu-radio
            v-model="formData.sql"
            v-for="item of windowSql"
            :key="item.id"
            :value="item.id"
            :label="item.name"
          ></mu-radio>
        </mu-form-item>
        <mu-form-item prop="middleware" label="中间件">
          <mu-radio
            v-model="formData.middleware"
            v-for="item of middleware"
            :key="item.id"
            :value="item.id"
            :label="item.name"
          ></mu-radio>
        </mu-form-item>
        <el-divider content-position="left">
          <span class="batchLinux-divider">自定义设置</span>
        </el-divider>
        <mu-form-item label="TCP" prop="tcp" :rules="tcpRules">
          <mu-text-field
            v-model="formData.tcp"
            prop="tcp"
            placeholder="请输入希望进行伪装的黑洞端口号,输入多个端口以逗号分隔"
            :max-length="50"
          ></mu-text-field>
        </mu-form-item>
        <mu-form-item label="UDP" prop="udp" :rules="udpRules">
          <mu-text-field
            v-model="formData.udp"
            prop="udp"
            placeholder="请输入希望进行伪装的黑洞端口号,输入多个端口以逗号分隔"
            :max-length="50"
          ></mu-text-field>
        </mu-form-item>
        <mu-form-item>
          <mu-button color="primary" small @click="openUsePort"
            >查看常用端口号</mu-button
          >
          <mu-button color="primary" small v-if="isOpen" @click="submitOpen"
            >开启</mu-button
          >
          <mu-button color="primary" small v-if="!isOpen" @click="submitEdit"
            >保存</mu-button
          >
          <mu-button @click="cancel" small>取消</mu-button>
        </mu-form-item>
      </mu-form>
    </mu-container>
  </div>
</template>
<script>
import {
  disguiseOpen,
  editConfig,
  getInfo
} from "req/api/hostservice/disguise.js";
import usePort from "./use_port";
export default {
  name: "hostservice_disguise_batchLinux",
  data() {
    return {
      labelPosition: "right",
      tcpRules: [
        {
          validate: val => this.$validates.checkMorePort(val),
          message: "端口号格式不正确,端口号可为多个,用逗号隔开且端口号必须大于0小于65536"
        },
        {
          validate: val => {
            let array = val ? val.toString().split(",") : [];
            return !this.$validates.isSelfRepeat(array);
          },
          message: "TCP端口号不能重复"
        },
        {
          validate: val => {
            let arr1 = val ? val.toString().split(",") : [];
            let arr2 = this.formData.udp
              ? this.formData.udp.toString().split(",")
              : [];
            return !this.$validates.isRepeat(arr1, arr2);
          },
          message: `TCP与UDP端口号不能重复`
        },
        {
          validate: val => {
            let arr1 = val ? val.toString().split(",") : [];
            let port = [
              "1098",
              "7001",
              "9060",
              "3306",
              "1521",
              "1433",
              "3389",
              "445",
              "135",
              "22",
              "5901"
            ];
            return !this.$validates.isRepeat(arr1, port);
          },
          message: `端口号不能与以下端口号相同["1098","7001","9060","3306","1521","1433","3389","445","135","22","5901"]`
        }
      ],
      udpRules: [
        {
          validate: val => this.$validates.checkMorePort(val),
          message: "端口号格式不正确,端口号可为多个,用逗号隔开且端口号必须大于0小于65536"
        },
        {
          validate: val => {
            let array = val ? val.toString().split(",") : [];
            return !this.$validates.isSelfRepeat(array);
          },
          message: "UDP端口号不能重复"
        },
        {
          validate: val => {
            let arr1 = val ? val.toString().split(",") : [];
            let arr2 = this.formData.tcp
              ? this.formData.tcp.toString().split(",")
              : [];
            return !this.$validates.isRepeat(arr1, arr2);
          },
          message: `TCP与UDP端口号不能重复`
        },
        {
          validate: val => {
            let arr1 = val ? val.toString().split(",") : [];
            let port = [
              "1098",
              "7001",
              "9060",
              "3306",
              "1521",
              "1433",
              "3389",
              "445",
              "135",
              "22",
              "5901"
            ];
           return !this.$validates.isRepeat(arr1, port);
          },
          message: `端口号不能与以下端口号相同["1098","7001","9060","3306","1521","1433","3389","445","135","22","5901"]`
        }
      ],
      formData: {
        id: this.disguise_id,
        host_id: this.disguise_id,
        standard: [],
        sql: 0,
        middleware: 0,
        tcp: "",
        udp: ""
      },
      linuxStandard: [
        {
          id: 3389,
          name: "Windows远程桌面"
        },
        {
          id: 445,
          name: "Windows文件与打印共享"
        },
        {
          id: 135,
          name: "Windows网络名称查询与连接"
        }
      ],
      windowStandard: [
        {
          id: 22,
          name: "SSH服务"
        },
        {
          id: 5901,
          name: "VNC服务器"
        }
      ],
      linuxSql: [
        {
          id: 1433,
          name: "Microsoft SQL Server"
        },
        {
          id: 1521,
          name: "Oracle"
        },
        {
          id: 3306,
          name: "MySQL"
        },
        {
          id: 0,
          name: "无"
        }
      ],
      windowSql: [
        {
          id: 1521,
          name: "Oracle"
        },
        {
          id: 3306,
          name: "MySQL"
        },
        {
          id: 0,
          name: "无"
        }
      ],
      middleware: [
        {
          id: 1098,
          name: "JBOSS"
        },
        {
          id: 7001,
          name: "Weblogic"
        },
        {
          id: 9060,
          name: "Websphere"
        },
        {
          id: 0,
          name: "无"
        }
      ],
      repeatPort: ""
    };
  },
  props: {
    disguise_id: {
      type: String
    },
    isOpen: {
      type: Boolean,
      default: true
    },
    isLinux: {
      type: Boolean,
      default: true
    },
    layerid: {
      type: String
    }
  },
  created() {
    if (!this.isOpen) {
      this.__getInfo({
        id: this.disguise_id
      });
    }
    if (this.isOpen && this.isLinux) {
      this.formData.standard = [3389];
    } else {
      this.formData.standard = [22];
    }
  },
  methods: {
    submitOpen() {
      this.$refs.hostservice_disguise_batchLinux.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "hostservice",
            "hostservice_disguise",
            "确定开启吗？",
            () => {
              let data = JSON.parse(JSON.stringify(this.formData));
              data.standard = data.standard.join(",");
              this.__disguiseOpen(data);
            },
            this
          );
        }
      });
    },
    submitEdit() {
      this.$refs.hostservice_disguise_batchLinux.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "hostservice",
            "hostservice_disguise",
            "确定修改吗?",
            () => {
              let data = JSON.parse(JSON.stringify(this.formData));
              data.standard = data.standard.join(",");
              this.__editConfig(data);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "hostservice",
        "hostservice_disguise",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    openUsePort() {
      this.$layer.iframe({
        content: {
          content: usePort,
          parent: this,
          data: {
            select_data: {}
          }
        },
        area: ["1000px", "auto"],
        title: "常用端口号",
        shadeClose: false
      });
    },
    __getInfo(param) {
      getInfo(param)
        .then(res => {
          this.formData.middleware = parseInt(res.data.middleware);
          this.formData.sql = parseInt(res.data.sql);
          this.formData.standard = res.data.standard
            .toString()
            .split(",")
            .map(item => {
              return parseInt(item);
            });
          this.formData.tcp = res.data.tcp;
          this.formData.udp = res.data.udp;
        })
        .catch(err => {});
    },
    __disguiseOpen(param) {
      disguiseOpen(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "开启成功"
          });
          this.$layer.close(this.layerid);
          this.$parent.$refs.hostservice_disguise_table.reload();
        })
        .catch(err => {});
    },
    __editConfig(param) {
      editConfig(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "修改成功"
          });
          this.$layer.close(this.layerid);
          this.$parent.$refs.hostservice_disguise_table.reload();
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped lang="less">
.hostservice-disguise-batchLinux {
  width: 100%;
  padding: 10px 20px 0 20px;
}
</style>



