<template>
  <div class="hostservice-disguise">
    <div class="tagIcon-notice">
      <span>系统功能图标表示:</span>
      <span v-for="item in icon.tagIcon" :key="item.id">
        <svg class="icon_1x5" aria-hidden="true" v-html="item.icon" />
        {{ item.name }}
      </span>
    </div>
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="hostservice_disguise_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="reload()">刷新</el-button>
        <el-button type="danger" size="mini" @click="removeModule(selectData)"
          >移除主机</el-button
        >
        <el-button type="primary" size="mini" @click="addModule()"
          >添加主机</el-button
        >

        <el-button type="primary" size="mini" @click="open(selectData)"
          >批量开启</el-button
        >
        <el-button type="danger" size="mini" @click="close(selectData)"
          >批量关闭</el-button
        >
      </template>
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button
          type="primary"
          size="mini"
          v-if="props.data.row.disguise.guise_status == 0"
          :disabled="props.data.row.online == 0 || props.data.row.os == 0 ? true : false"
          @click="open(props.data.row)"
          >开启</el-button
        >
        <el-button
          type="danger"
          size="mini"
          v-if="props.data.row.disguise.guise_status == 1"
          :disabled="props.data.row.online == 0 || props.data.row.os == 0 ? true : false"
          @click="close(props.data.row)"
          >关闭</el-button
        >
        <el-button
          type="primary"
          size="mini"
          :disabled="props.data.row.os == 0 ? true : false"
          @click="openPortInformation(props.data.row)"
          >端口信息验证</el-button
        >
        <el-button
          type="primary"
          size="mini"
          :disabled="
            props.data.row.online == 0 || props.data.row.disguise.guise_status == 0 || props.data.row.os == 0
              ? true
              : false
          "
          @click="edit(props.data.row)"
          >参数设置</el-button
        >
      </template>
      <!-- 资产名称 -->
      <template v-slot:name_head="props">
        <el-input
          v-model="params.name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 状态 -->
      <template slot="online" slot-scope="props">
        <div v-if="props.data.row.online == 1">
          <span v-html="icon.linkIcon.link"></span>在线<span v-show="props.data.row.version">[v{{props.data.row.version}}]</span>
        </div>
        <div v-else-if="props.data.row.online == 0">
          <span v-html="icon.linkIcon.unlink"></span>离线<span v-show="props.data.row.version">[v{{props.data.row.version}}]</span>
        </div>
      </template>
      <template v-slot:online_head="props">
        <el-select
          v-model="params.online"
          @change="search"
          clearable
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.online"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- IP地址 -->
      <template slot="ip" slot-scope="props">
        <el-popover trigger="hover" placement="top">
          <p>主机名: {{ props.data.row.name }}</p>
          <p>业务系统名称: {{ props.data.row.software.name }}</p>
          <div slot="reference" class="name-wrapper">
            {{ props.data.row.ip }}
          </div>
        </el-popover>
      </template>
      <template v-slot:ip_head="props">
        <el-input
          v-model="params.ip"
          size="mini"
          :maxLength="20"
          placeholder="请输入内容"
          @keyup.enter.native="reload"
        ></el-input>
      </template>
      <!-- 已开功能 -->
      <template slot="disguise" slot-scope="props">
        <div v-if="props.data.row.disguise.guise_status == 0">暂无</div>
        <el-popover v-else trigger="hover" placement="top">
          <p>
            标准选项：{{ props.data.row.disguise.port.standard | filter_standard }}
          </p>
          <p>数据库服务器：{{ openDisguiseTag.sql[props.data.row.disguise.port.sql] || "无"  }}</p>
          <p>中间件：{{ openDisguiseTag.middleware[props.data.row.disguise.port.middleware] || "无"  }}</p>
          <p>TCP：{{ props.data.row.disguise.port.tcp || "无" }}</p>
          <p>UDP：{{ props.data.row.disguise.port.udp || "无" }}</p>
          <div slot="reference" class="name-wrapper">
            <span>
              <svg
                class="icon_1x5"
                aria-hidden="true"
                v-html="icon.tagIcon.biaozhunicon.icon"
              />
            </span>
            <span v-show="props.data.row.disguise.port.sql != 0">
              <svg
                class="icon_1x5"
                aria-hidden="true"
                v-html="icon.tagIcon.database.icon"
              />
            </span>
            <span v-show="props.data.row.disguise.port.middleware != 0">
              <svg
                class="icon_1x5"
                aria-hidden="true"
                v-html="icon.tagIcon.middleware.icon"
              />
            </span>
            <span
              v-show="
                props.data.row.disguise.port.tcp ||
                  props.data.row.disguise.port.udp
              "
            >
              <svg
                class="icon_1x5"
                aria-hidden="true"
                v-html="icon.tagIcon.zidingyi.icon"
              />
            </span>
          </div>
        </el-popover>
      </template>
      <template v-slot:disguise_head="props">
        <el-input
          v-model="params.guise_port"
          size="mini"
          :maxLength="20"
          placeholder="请输入内容"
          @keyup.enter.native="reload"
        ></el-input>
      </template>
      <!-- 操作系统版本 -->
      <template slot="os" slot-scope="props">
        <svg
          class="icon_1x5"
          aria-hidden="true"
          v-html="dropDownData.os[props.data.row.os].icon"
        />
        <span>{{ dropDownData.os[props.data.row.os].name }}</span>
      </template>
      <template v-slot:os_head="props">
        <el-select
          v-model="params.os"
          clearable
          @change="search"
          size="mini"
          placeholder="请选择"
        >
          <el-option
            v-for="item in dropDownData.os"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import { disguiseClose } from "req/api/hostservice/disguise";
import batchLinux from "views/hostservice/disguise/batch_linux";
import portInformation from "views/hostservice/disguise/port_information";
import { getDropDownData } from "req/api/assets/list";
import addModule from "views/hostservice/addModule";
import moduleRefresh from "common/mixins/modulRefresh"
import { delModule } from "req/api/hostservice/addModule";
export default {
  data() {
    return {
      url: "/host/list/disguise",
      type: "post",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "资产名称",
          sortable: true,
          prop: "name",
          second_head: {
            width: "200px",
            prop: "name",
            templateHeadName: "name_head"
          }
        },
        {
          label: "IP地址",
          sortable: true,
          prop: "ip",
          sortMethod: this.$tools.IPSort,
          second_head: {
            width: "150px",
            isTemplate: true,
            templateName: "ip",
            prop: "ip",
            templateHeadName: "ip_head"
          }
        },
        {
          label: "系统列表",
          prop: "os",
          sortable: true,
          second_head: {
            width: "180px",
            isTemplate: true,
            templateName: "os",
            templateHeadName: "os_head",
            prop: "os"
          }
        },
        {
          label: "已开功能",
          prop: "disguise",
          sortable: true,
          second_head: {
            width: "200px",
            isTemplate: true,
            templateName: "disguise",
            templateHeadName: "disguise_head",
            prop: "disguise"
          }
        },
        {
          label: "状态",
          prop: "online",
          sortable: true,
          second_head: {
            width: "150px",
            isTemplate: true,
            templateName: "online",
            templateHeadName: "online_head",
            prop: "online"
          }
        },
        {
          label: "操作",
          second_head: {
            label: "",
            minWidth: "350px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        name: "",
        ip: "",
        os: "",
        status: "",
        guise_port: "",
        online:""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        online: [
          {
            id: 1,
            name: "在线"
          },
          {
            id: 0,
            name: "离线"
          }
        ],
        os: this.$icons.osIcon,
        tag: []
      },
      icon: {
        linkIcon: this.$icons.linkIcon,
        tagIcon: this.$icons.tagIcon
      },
      selectData: [],
      openDisguiseTag: {
        middleware: {
          1098: "JBOSS",
          7001: "Weblogic",
          9060: "Websphere",
          0: "无"
        },
        sql: {
          1433: "Microsoft SQL Server",
          1521: "Oracle",
          3306: "MySQL",
          0: "无"
        }
      }
    };
  },
  created() {
    this.__getDropDownData();
  },
  filters: {
    filter_standard(value) {
      if(!value){
        return '无'
      } 
      value = value.toString()
      let arr = value.split(",");
      let newArr = [];
      let standard =  {
          3389: "Windows远程桌面",
          445: "Windows文件与打印共享",
          135: "Windows网络名称查询与连接",
          22: "SSH服务",
          5901: "VNC服务器"
        }
      for (let item of arr) {
        if(standard[item]) {
          newArr.push(standard[item])
        }
      }
      return newArr.join(',');
    }
  },
  mixins: [moduleRefresh("hostservice_disguise_table")],
  methods: {
    search() {
      this.params.page = 1;
      this.reload();
    },
    selectChange(select) {
      this.selectData = select;
    },
    addModule() {
      this.$layer.iframe({
        content: {
          content: addModule,
          parent: this,
          data: {
            moduleType: "disguise",
            tipText: "hostservice_disguise"
          }
        },
        area: ["700px", "auto"],
        title: "添加主机 ( 列表中将自动过滤与本模块冲突的资产 )",
        shadeClose: false
      });
    },
    removeModule(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.$tools.isArrayNull(item)) {
          this.$message({
            type: "error",
            message: "请选择需要移除的主机!"
          });
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id;
      }
      this.$isShowTip(
        "hostservice",
        "hostservice_disguise",
        "确定移除吗?",
        () => {
          this.__delModule({ host_id: ids, module: "disguise" });
        },
        this
      );
    },
    __delModule(param) {
      delModule(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "移除成功"
          });
          this.reload();
        })
        .catch(err => {});
    },
    open(item) {
      let openDisguiseType = {
        linux: true,
        window: false
      };
      if (Array.isArray(item)) {
        if (this.isBatchSelectNull(item)) {
          return;
        }
        let isOpenData = this.isBatchOpenDisguise(item);
        if (isOpenData.errmsg) {
          this.$message({
            type: "error",
            message: isOpenData.errmsg
          });
          return;
        }
        this.openDisguise(
          isOpenData.ids,
          true,
          openDisguiseType[isOpenData.type]
        );
      } else {
        let type = "";
        switch (item.os) {
          case 1:
            type = "window";
            break;
          case 2:
            type = "linux";
            break;
        }
        this.openDisguise(item.id.toString(), true, openDisguiseType[type]);
      }
    },
    openPortInformation(item) {
      this.$layer.iframe({
        content: {
          content: portInformation,
          parent: this,
          data: {
            layer_data: item
          }
        },
        area: ["700px", "auto"],
        title: "端口信息验证",
        shadeClose: false
      });
    },
    edit(item) {
      if (item.os == 1) {
        this.openDisguise(item.id.toString(), false, false, "参数设置");
      } else if (item.os == 2) {
        this.openDisguise(item.id.toString(), false, true, "参数设置");
      }
    },
    close(item) {
      let ids = "";
      if (Array.isArray(item)) {
        if (this.isBatchSelectNull(item)) {
          return;
        }
        ids = this.$tools.mergeId(item);
      } else {
        ids = item.id.toString();
      }
      this.$isShowTip(
        "assets",
        "assets_disguise",
        "确定关闭吗?",
        () => {
          this.__disguiseClose({ host_id: ids });
        },
        this
      );
    },
    openDisguise(ids, isOPen, isLinux, title) {
      this.$layer.iframe({
        content: {
          content: batchLinux,
          parent: this,
          data: {
            disguise_id: ids,
            isOpen: isOPen,
            isLinux: isLinux
          }
        },
        area: ["800px", "auto"],
        title: title
          ? title
          : isLinux === true
          ? "Linux系统伪装"
          : "Windows系统伪装",
        shadeClose: false
      });
    },
    isBatchOpenDisguise(item) {
      let oDisguiseData = {
        unknown: [],
        linux: [],
        window: []
      };
      let isOpenData = {};
      for (let disguise of item) {
        switch (disguise.os) {
          case 0:
            oDisguiseData.unknown.push(disguise.id);
            isOpenData.errmsg = "未知系统不能伪装!";
            break;
          case 1:
            oDisguiseData.window.push(disguise.id);
            break;
          case 2:
            oDisguiseData.linux.push(disguise.id);
            break;
        }
      }

      if (!isOpenData.msg) {
        if (oDisguiseData.linux.length == item.length) {
          isOpenData.type = "linux";
          isOpenData.ids = oDisguiseData.linux.join(",");
        } else if (oDisguiseData.window.length == item.length) {
          isOpenData.type = "window";
          isOpenData.ids = oDisguiseData.window.join(",");
        } else {
          isOpenData.errmsg = "请选择相同的系统进行伪装!";
        }
      }
      return isOpenData;
    },
    isBatchSelectNull(array) {
      if (this.$tools.isArrayNull(array)) {
        this.$message({
          type: "error",
          message: "请选择选项!"
        });
        return true;
      } else {
        return false;
      }
    },
    __getDropDownData() {
      getDropDownData()
        .then(res => {
          this.dropDownData.tag = res.data.tag;
        })
        .catch(err => {});
    },
    __disguiseClose(param) {
      disguiseClose(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "关闭成功!"
          });
          this.reload();
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less" scoped>
.hostservice-disguise {
  .tagIcon-notice {
    // height: 50px;
    line-height: 50px;
    span {
      margin-right: 10px;
      svg {
        vertical-align: middle;
      }
    }
  }
}
</style>



